<template>
  <div class="search-list-page">
    <div class="search-list-title">
      <div></div>
      <div>歌名</div>
      <div>歌手</div>
      <div>专辑</div>
    </div>
    <div class="search-list-box" ref="searchListBoxDom">
      <div class="search-list-item" v-for="(item, index) in albumMusicList" :key="index"
        :style="
          currentList.length === 0
            ? ''
            : currentList[currentListIndex].id === item.id
            ? playingSongStyle
            : ''
        ">
		
        <div class="mark-img">
          <img :src="setMarkImgUrl(item)" @click="markSong(item, index)" width="18px"/>
        </div>
        <div class="search-list-song-name" @click="playThisSong(item, index)">
          {{ item.name }}
        </div>
        <div class="search-list-singer-name" @click="playThisSong(item, index)">
          {{ item.artists[0].name }}
        </div>
        <div class="search-list-album-name" @click="playThisSong(item, index)">
          {{ item.album.name }}
        </div>
      </div>
	  
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "search-list-page",
  data() {
    return {
      markImgUrl: require("@/assets/mark.svg"),
      markedImgUrl: require("@/assets/marked.svg"),
      playingSongStyle: {
        "background-color": "rgba(0,0,0,.33)",
        "border-radius": "20px 20px",
      },
    };
  },
  computed: {
    ...mapState({
      currentList: (state) => state.currentList,
      currentListIndex: (state) => state.currentListIndex,
      albumMusicList: (state) => state.albumMusicList,
      markedList: (state) => state.markedList,
    }),
  },
  methods: {
	// 播放歌曲
    playThisSong(item, index) {
      var payload = {
        index: index,
        activatedPage: 'albumListPage'
      }

      this.$store.commit("sendCurrentIndex", payload)
      this.$store.commit("playCurrentSong");
    },
	// 收藏歌曲
    markSong(item, index) {
      this.$store.commit("sendMarkedSong", item);
    },
	// 标记
    setMarkImgUrl(item) {
      // 将已mark的歌曲逐一与搜索列表对比，如有mark中的歌曲，则红心点亮
      var haveMatchedId = false;
      for (let i = 0; i < this.markedList.length; i++) {
        if (item.id === this.markedList[i].id) {
          haveMatchedId = true;
        }
      }
      return haveMatchedId === true ? this.markedImgUrl : this.markImgUrl;
    }
  }
};
</script>

<style>
.search-list-page {
  width: 95%;
  height: 100%;
  margin-left: 5%;
}

.search-list-title {
  width: 100%;
  height: 20px;
  line-height: 20px;
  display: flex;
  font-weight: bold;
  color: white;
}

.search-list-title div:nth-child(1) {
  width: 4%;
}

.search-list-title div:nth-child(2) {
  width: 40%;
}

.search-list-title div:nth-child(3) {
  width: 16%;
}

.search-list-title div:nth-child(4) {
  width: 40%;
}

.search-list-box {
  width: 100%;
  height: 95%;
  overflow-x: hidden;
}

.search-list-backtop {
  width: 30px;
  height: 30px;
  margin-left: 715px;
  margin-top: 570px;
  position: fixed;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
}

.search-list-backtop:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.search-list-backtop:active {
  transform: scale(1.2);
}

.search-list-item {
  height: 40px;
  line-height: 40px;
  display: flex;
  color: rgba(225,225,225,.9);
}
.search-list-item:hover{
  background-color:rgba(0,0,0,.33);
  border-radius: 20px 20px;
}

.mark-img {
  width: 4%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mark-img:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.mark-img:active {
  transform: scale(1.5);
}

.search-list-song-name {
  width: 40%;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.search-list-singer-name {
  width: 16%;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.search-list-album-name {
  width: 40%;
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.load-more {
  width: 95%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin-top: 5px;
  border-radius: 20px 20px;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  cursor: pointer;
}

.load-more:hover {
  background-color: rgba(0,0,0,0.66);
}
</style>
